@mixin fontSize($sizeValue){
    font-size: $sizeValue * 1px;
    font-size: calc($sizeValue / 10) * 1rem;
}
footer {
	background: #FEFA52;
	padding-bottom: 20px;
	padding-top: 30px;
	.container {
		max-width: 1200px;
		margin: 0 auto;
		padding: 5px;
		@extend .flex;
		align-items:end;
		.logo {
			width: 13%;
			a {
				width: 102px;
				display: block;
		}
			
			// margin-left: auto;
		}
	}
	.foundationDetails, .vegaInfo {
		width: 40%;
	}
	.vegaInfo {
		font-family: 'VEGAB-Regular',serif;
		text-align: right;
		text-transform: uppercase;
		@include fontSize(12);
		.social_links ul {
			list-style: none;
			padding: 0;
		}
		h2 {
			font-family: 'VegaABC', serif;
			text-transform: uppercase;
			@include fontSize(16);
			margin: 10px 0;
		}

	}
}
.foundationDetails {
	p {
		margin: 0;
		margin-top:10px;
	}
}